.mianshi-container {
  div {
    box-sizing: border-box;
  }
  width: 100%;
  box-sizing: border-box;
  .mianshi-content {
    flex: 1;
    box-sizing: border-box;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    max-height: 490px;
    background-color: #fff;
    // background-color: #000;
    .top-header {
      display: flex;
      padding: 0px 0px 0px 20px;
      height: 60px!important;
      align-items: center;
      img {
        width: 26px;
        margin-right: 10px;
      }
      p {
        font-family: MicrosoftYaHeiSemibold;
        font-size: 14px;
        color: #212121;
        letter-spacing: 0;
        line-height: 20px;
        font-weight: 600;
      }
    }
    .main-detail {
      flex: 1;
      overflow-y: scroll;
      word-break: break-all;
      word-wrap: break-word;
      padding: 5px 20px 20px 20px;
      .kd-divider-inner-text {
        // width: 100%;
        word-break: keep-all;
        font-family: MicrosoftYaHei;
        font-size: 12px;
        color: #999999;
        letter-spacing: 0;
        font-weight: 400;
      }
      .history-detail {
        // white-space: pre-line;
        // font-family: MicrosoftYaHei;
        // font-size: 14px;
        // color: #212121;
        // letter-spacing: 0;
        // line-height: 24px;
        // font-weight: 400;
        // width: 100%;
        .kd-divider {
          // width: 100%;
        }
        
        p {
          margin: 0;
          font-family: MicrosoftYaHei;
          font-size: 14px;
          color: #212121;
          letter-spacing: 0;
          line-height: 24px;
          white-space: pre-line;
          font-weight: 400;
        }
      }
      .new-detail {
        white-space: pre-line;
        font-family: MicrosoftYaHei;
        font-size: 14px;
        color: #212121;
        letter-spacing: 0;
        line-height: 24px;
        font-weight: 400;
      }
    }
  }
  .mianshi-bottom {
    margin-top: 16px;
    .kd-btn {
      border-color: #5582F3;
      color: #5582F3;
    }
    .loading {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      .loading-left {
        display: flex;
        align-items: center;
        span {
          font-family: MicrosoftYaHei;
          font-size: 14px;
          color: #212121;
          letter-spacing: 0;
          line-height: 21px;
          font-weight: 400;
          margin-left: 10px;
        }
      }
      .loading-stop {
         cursor: pointer;
        .stop-tingzhi {
          font-family: MicrosoftYaHei;
          font-size: 14px;
          color: #666666;
          letter-spacing: 0;
          line-height: 20px;
          font-weight: 400;
        }
        .stop-esc {
          font-family: ArialMT;
          font-size: 12px;
          color: #B2B2B2;
          letter-spacing: 0;
          line-height: 20px;
          font-weight: 400;
          margin-left: 4px;
        }
      }
    }
  }
}
body {
  background-color: #ccc;
}